@charset "UTF-8";
/* http://meyerweb.com/eric/tools/css/reset/ 
   v2.0 | 20110126
   License: none (public domain)
*/
@import url("https://fonts.googleapis.com/css2?family=Noto+Sans+TC:wght@400;500&family=Roboto:wght@400;500&display=swap");
html, body, div, span, applet, object, iframe,
h1, h2, h3, h4, h5, h6, p, blockquote, pre,
a, abbr, acronym, address, big, cite, code,
del, dfn, em, img, ins, kbd, q, s, samp,
small, strike, strong, sub, sup, tt, var,
b, u, i, center,
dl, dt, dd, ol, ul, li,
fieldset, form, label, legend,
table, caption, tbody, tfoot, thead, tr, th, td,
article, aside, canvas, details, embed,
figure, figcaption, footer, header, hgroup,
menu, nav, output, ruby, section, summary,
time, mark, audio, video {
  margin: 0;
  padding: 0;
  border: 0;
  font-size: 100%;
  font: inherit;
  vertical-align: baseline; }

/* HTML5 display-role reset for older browsers */
article, aside, details, figcaption, figure,
footer, header, hgroup, menu, nav, section {
  display: block; }

body {
  line-height: 1; }

ol, ul {
  list-style: none; }

blockquote, q {
  quotes: none; }

blockquote:before, blockquote:after,
q:before, q:after {
  content: '';
  content: none; }

table {
  border-collapse: collapse;
  border-spacing: 0; }

/* 常用css */
/* 
*{outline: solid;
} */
*, *::before, *::after {
  box-sizing: border-box; }

img {
  max-width: 100%;
  height: auto;
  vertical-align: middle; }

a {
  text-decoration: none;
  display: block; }

body {
  font-family: 'Roboto','Noto Sans TC', sans-serif;
  line-height: 1.5; }

.material-icons-outlined {
  font-size: inherit; }

header {
  background: #AA0601; }
  header img {
    width: 80px;
    padding-top: 16px;
    padding-bottom: 15.43px; }

.header {
  display: flex;
  justify-content: space-between; }

.hamburger {
  display: none;
  color: #FFFFFF;
  background-color: transparent;
  border: 0;
  font-size: 40px;
  padding: 0; }
  .hamburger :first-child {
    display: block; }
  .hamburger :last-child {
    display: none; }

.opened :first-child {
  display: none; }

.opened :last-child {
  display: block; }

.nav ul {
  display: flex; }

.nav a {
  padding: 25px 32px;
  font-size: 20px;
  color: #FFFFFF; }
  .nav a:hover {
    font-weight: 500;
    opacity: 0.8; }

@media screen and (max-width: 769px) {
  header img {
    padding-top: 12px;
    padding-bottom: 11.43px; }
  .hamburger {
    display: block; }
  .header {
    flex-wrap: wrap;
    align-items: center; }
    .header .nav {
      display: none;
      width: calc(100% + 24px);
      margin-left: -12px;
      margin-right: -12px; }
      .header .nav a {
        font-size: 16px;
        padding: 12px;
        text-align: center;
        display: block; }
    .header .open {
      display: block; }
    .header ul {
      flex-wrap: wrap;
      align-items: center; }
    .header li {
      width: 50%;
      border: 1px solid #FFFFFF;
      border-top: 1px solid #FFFFFF;
      border-left: 1px solid #FFFFFF; } }

.container {
  margin: 0 auto;
  padding-left: 12px;
  padding-right: 12px;
  max-width: 1320px; }
  @media screen and (max-width: 769px) {
    .container {
      max-width: 720px; } }
  @media screen and (max-width: 425px) {
    .container {
      max-width: 373px; } }

.menu {
  display: flex;
  border-left: 1px solid #DCDCDC; }
  .menu li {
    width: 33.33333%;
    text-align: center;
    border-right: 1px solid #DCDCDC; }
  .menu a {
    font-size: 32px;
    color: #000000;
    font-weight: 500;
    padding-top: 24px;
    padding-bottom: 16px;
    border-bottom: 8px solid transparent; }
    .menu a:hover {
      color: #AA0601; }
  .menu .active {
    border-bottom: 8px solid #AA0601; }

.img2 {
  display: flex;
  margin-bottom: 80px; }
  .img2 .box {
    width: 50%;
    height: 296px; }
    .img2 .box img {
      height: 100%;
      width: 100%;
      object-fit: cover; }

.title {
  text-align: center;
  margin-bottom: 32px; }
  .title h2 {
    font-size: 42px;
    font-weight: 500; }
  .title h3 {
    font-size: 32px; }

.card {
  display: flex;
  flex-wrap: wrap;
  margin-right: -12px;
  margin-left: -12px; }
  .card li {
    width: 25%;
    padding-right: 12px;
    padding-left: 12px; }
  .card .card-body {
    display: flex;
    flex-wrap: wrap;
    justify-content: space-between; }
    .card .card-body h4 {
      font-size: 24px; }
    .card .card-body p {
      font-size: 24px;
      color: #AA0601; }
  .card .color {
    display: flex;
    margin-bottom: 80px; }
  .card .brown {
    width: 24px;
    height: 24px;
    border-radius: 4px;
    margin-right: 8px;
    background: #5F3E2D; }
  .card .orange {
    width: 24px;
    height: 24px;
    border-radius: 4px;
    margin-right: 8px;
    background: #B75929; }

.pagination {
  display: flex;
  justify-content: center;
  line-height: 1.333333333333333;
  margin-bottom: 80px; }
  .pagination li {
    border: 1px solid #555555; }
  .pagination .prev {
    padding: 10.3px 11px;
    font-size: 19px;
    color: #555555; }
  .pagination .number {
    padding: 10.9px 14.5px;
    font-size: 18px;
    color: #555555; }
  .pagination .next {
    padding: 10.3px 11px;
    font-size: 19px;
    color: #555555;
    background: #F2F2F2; }
  .pagination .active {
    background: #000000; }
    .pagination .active .number {
      color: #FFFFFF; }

@media screen and (max-width: 769px) {
  nav.container {
    max-width: 100%;
    padding: 0; }
  .card li {
    width: 50%; }
  .menu a {
    font-size: 16px;
    padding-top: 16px;
    padding-bottom: 8px; } }

@media screen and (max-width: 425px) {
  .img2 {
    margin-bottom: 40px; }
    .img2 .box {
      height: 136px; }
  .title {
    margin-bottom: 24px; }
    .title h2 {
      font-size: 24px; }
    .title h3 {
      font-size: 24px; }
  .card {
    margin-bottom: 40px; }
    .card li {
      width: 100%; }
    .card .color {
      margin-bottom: 24px; }
  .pagination {
    margin-bottom: 64px; }
    .pagination .prev {
      font-size: 16px; }
    .pagination .next {
      font-size: 16px; }
    .pagination .number {
